import React from 'react';


export const Eye = ({typeOfPasword, showPassword}) => (
  <>
    {typeOfPasword==='password' 
      ? (<i 
          className="eye fa fa-eye" 
          onClick = {() => showPassword()}
        />)
      : (<i 
          className="eye fa fa-eye-slash" 
          onClick = {() => showPassword()}
        />)
    }
  </>
)